.main{
    padding-bottom: 85px;
  
    
}

.header{
    
    min-height: 80px;
    background-image: url('../../img/header-bg.svg');
    background-repeat: no-repeat;
    position: fixed;
    width: 100%;
    box-sizing: content-box;
    top: 0;
    z-index: 9;
    background-size: contain;

    .back{
        position: absolute;
        
        width: 27px;
        background-image: url('../../img/icon-back.svg');
        top: 18px;
        left: 12px;
        background-repeat: no-repeat;
        height: 31px;
        background-size: contain;
        z-index: 99;

    }

    h3{
        line-height: 70px;
        font-size: 18px;
        padding-left: 46px;
        position: relative;
    }
}

.login-wrapper{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
    .intro{
        font-size: 25px;
        font-weight: bold;
        text-align: center;
        margin: 60px;
        flex: 1;
        
    }

    .login-footer{
        margin-bottom: 60px;
        text-align: center;
    }
}

.user__img{
    width: 60px;
    height: 60px;
    margin: auto;
    border-radius: 50%;
    background-color: #ccc;
    border: 3px solid #34B766;
}

.user__level{
    height: 80px;
    
    margin: auto;
    color: #fff;
    // background-color: #34B766;
    
    
    margin-top: -24px;
    position: relative;
    z-index: -1;
    // padding: 38px 0 0;
    text-align: center;
    font-size: 15px;

    span{
        display: inline-block;
        margin-top: 18px;
        padding-top: 5px;
    }

    &::before{
        position: absolute;
        bottom: -0px;
        content: '';
        height: 80px;
        width: 70px;
        background-image: url('../../img/triangle.svg');
        background-size: contain;
    background-repeat: no-repeat;
    left: 0px;
    right: 0;
    margin: auto;
    z-index: -2;
    }
}

.user__name{
    text-align: center;
    margin-bottom: 5px;
}

.chest-container{
    display: flex;
    margin-top: 20px;
    margin: 120px 5px 0 5px;
    
    .chest{
        
        flex: 1;
        border-radius: 6px;
        color: #fff;
        margin: 10px 4px;
        padding: 10px 4px  24px 50px;
        position: relative;
        text-transform: uppercase;
        min-height: 75px;

        &--free{

           
background: #4eff00; /* Old browsers */
background: -moz-linear-gradient(top,  #4eff00 0%, #038817 100%); /* FF3.6-15 */
background: -webkit-linear-gradient(top,  #4eff00 0%,#038817 100%); /* Chrome10-25,Safari5.1-6 */
background: linear-gradient(to bottom,  #4eff00 0%,#038817 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */



position: relative;

            &::before{
                position: absolute;
                content: '';
                height: 45px;
                width: 45px;
                background-size: cover;
                left: 0;
                top: -8px;
                background-image: url('../../img/graduation.svg');
                background-repeat: no-repeat;
            }
        }

        &--game{
            background: linear-gradient(to bottom, #fbf605 1%, #ffad01 54%, #de7b00fa 100%);
            position: relative;

            &::before{
                position: absolute;
                content: '';
                height: 45px;
                width: 45px;
                background-size: cover;
                left: 0;
                top: -8px;
                background-image: url('../../img/diploma.svg');
                background-repeat: no-repeat;
            }
        }

        &__loader-wrap{
            width: 100%;
            right: 0;
            left: 0;
            bottom: 0px;
            background-color: rgba(0, 0, 0, 0.14);
            padding: 7px 0;
             border-bottom-left-radius: 5px;
                border-bottom-right-radius: 5px;
         
            margin: auto;
            position: absolute;

            span{
                text-align: center;
                display: block;
            }
            
            .loading-bar{
                width: 60%;
                height: 10px;
/* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#fceabb+0,fccd4d+50,f8b500+51,fbdf93+100;Orange+3D+%235 */
background: #fceabb; /* Old browsers */
background: -moz-linear-gradient(top,  #fceabb 0%, #fccd4d 50%, #f8b500 51%, #fbdf93 100%); /* FF3.6-15 */
background: -webkit-linear-gradient(top,  #fceabb 0%,#fccd4d 50%,#f8b500 51%,#fbdf93 100%); /* Chrome10-25,Safari5.1-6 */
background: linear-gradient(to bottom,  #fceabb 0%,#fccd4d 50%,#f8b500 51%,#fbdf93 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */

                margin-left: 0px;
                border-bottom-left-radius: 3px;
                border-top-left-radius: 3px;
                width: 85%;
                margin-bottom: 3px;
            }

            .loading-bg{
                background: #000;
               
                margin: auto;
                height: 10px;
                width: 85%;
                border-radius: 3px;

                span{
                    position: relative;
                    z-index: 9;
                    position: absolute;
                    font-size: 11px;
                    bottom: 10px; 
                }
            }
        }

        &__status{
            margin-left: 4px;
            h3{
                margin: 0;
                font-size: 13px;
                font-weight: bold;
            }

            p{
                font-size: 12px;
                margin: 3px 0;
            }
        }
    }
    
}


.promo-cards{
    margin: 20px 10px;
    background-image: url('../../img/pattern-red.svg');
    
    background-size: cover;
    border-radius: 8px;
    padding: 30px 10px 30px 100px;
    color: #fff;

    h3{
        font-size: 20px;
    }

    p{
        font-size: 13px;
        font-family: $light-font;

        font-weight: 300;
        
        font-style: normal;
    }
}

.categoriies-header {
    display: flex;
    justify-content: space-between;
    margin: 10px 10px;
    h2{
        font-size: 24px;
    }

    .view-all{

    }
}

.categories{
    display: flex;
    margin: 0 10px;
    flex-wrap: wrap;
    // overflow-x: scroll;
    
    .category{
        margin-bottom: 12px;
        min-width: 25%;
        &__icon{
            margin: auto;
            height: 60px;
            width: 60px;
            border-radius: 50%;
            margin-bottom: 6px;
        }

        &__name{
            text-align: center;
        }
    }
}

.footer{
    width: 100%;
    position: fixed;
    bottom: 0;
    height: 60px;
    display: flex;
    justify-content: space-between;

    &::before{
        content: '';
        position: fixed;
        width: 105vw;
        background-image: url("../../img/footer-bg.svg");
        background-repeat: no-repeat;
        right: 0;
        bottom: -30px;
        left: 0px;
        z-index: 0;
        height: 100px;
        background-size: contain;
    }

    .btn{
        
        z-index: 9;
        position: relative;
    }

 
    &__item{
        display: block;
        padding: 5px 10px;
        // line-height: 40px;
        position: relative;
        z-index: 0;
        color: #000;
    }

    .footer-icon{
        height: 20px;
        width: 20px;
        
        margin: auto;
        margin-bottom: 4px;
        
        display: block;
        

    }
}

.footer.no-bg{
    &::before{
        background-image: none;
    }
}



.category-header{
    background-image: url('../../img/orange-circles.svg');
    min-height: 120px;
    background-repeat: no-repeat;
    padding-top: 80px;
    padding-bottom: 30px;
    margin-top: 50px;
    color: #fff;
    text-align: center;
    background-size: cover;

    h3{
        font-size: 25px; 
    }

    p{
        font-family: neusa-next-std-wide, sans-serif;

        font-weight: 300;
        max-width: 280px;
        margin: auto;
        font-size: 14px;
    }
}

.rankings{
    // margin: 20px;
    counter-reset: section;
   h3{
       font-size: 18px;
       margin: 20px 10px;
   }

   .ranking-list{
       display: flex;
       justify-content: space-between;
       align-items: center;
       padding-top:6px;
       padding-bottom:6px;
       padding-left: 40px;
       padding-right: 30px;

       position: relative;
       margin-bottom: 4px;

       &:nth-child(odd){
        background-color: #EFF0F1;
       }
       


       &__img{
        height: 40px;
        width: 40px;
        background-color: #ccc;
        
        border-radius: 50%;
       }

       &__name{
        flex: 1;
        margin-left: 11px;
       }

       &__points{
        
        position: relative;
        padding-left: 20px;

        &::before {
            position: absolute;
            content: '';
            height: 20px;
            width: 20px;
            background-image: url('../../img/coin.svg');
            background-size: cover;
            left: -8px;
            top: -2px;
        }

       }

       &::before {
        position: absolute;
        counter-increment: section;
        content:counter(section);
        left: 20px;
        


      }
   }
}


.pick-opponent{
    margin-top: 120px;
    margin: 120px 10px 10px 10px;

    h4{
        font-family: neusa-next-std-wide, sans-serif;
        font-weight: 300;
        font-size: 15px;
        margin-bottom: 10px;
    }
   
    p{
        font-size: 13px;
        margin-bottom: 6px;
        font-family: neusa-next-std-wide, sans-serif;
        font-weight: 300;
    }

    .btn{
        margin: auto;
        margin-bottom: 10px;
        
    }
}

.opponents{
    margin: 10px 0;
    display: flex;
    
    flex-wrap: wrap;
    .opponent{
        max-width: 25%;
        text-align: center;
        font-size: 11px;
        margin-bottom: 15px;
        font-family: neusa-next-std-wide, sans-serif;
        font-weight: 300;
        &__image{
            margin: auto;
            height: 40px;
            width: 40px;
            background-color: #ccc;
            border-radius: 50%;
            margin-bottom: 6px;
        }

        &__name{
            max-width: 100px;
        }
    }
}

.opponent-buttons{
    
    margin-top: 60px;

    .btn{
        margin-bottom: 20px;
    }
}

.search-container{
    .input{
        border-radius: 50px;
        height: 50px;
        margin-bottom: 30px;
    }
}

.overlay{
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.79);
    position: fixed;
    z-index: 999999;
    top: 0;
    display: flex;
    display: none;
  

    .details{
        background-color:#fff;
        border-top-left-radius: 30px;
        border-top-right-radius: 30px;
        height: 70vh;
        width: 100%;
        position: absolute;
        bottom: 0;
        padding: 10px 30px;
        justify-content: space-between;
        display: flex;
        flex-direction: column;

        .profile{
            width: 100%;
            justify-content: space-between;
            
            
            h3{
                margin-top: 66px;
                font-size: 34px;
            }
        }

     

        .user__level{
            z-index: 1;
        }

        .user__img{
            z-index: 2;
            position: relative;
        }

        .winner-reward{
            text-align: center;
            margin-top: 60px;
            
            flex: 1;

            h3{
                font-family: $light-font;
                font-size: 17px;
            }
     
            .reward-wrap{
                display: flex;
                max-width: 290px;
                margin: auto;
                justify-content: space-between;
                font-size: 22px;
                

                .reward{
                    border: 1px solid #eee;
                    margin: 10px 0;
                    flex: 1;
                    position: relative;
                    
                }

                .reward--gem, .reward--coin{
                    padding: 16px 30px;
                    


                    &:before{
                    content: '';
                    height: 30px;
                    width: 30px;
                    
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    margin: auto;
                    right: 0;
                }
            }
            .reward--gem{
                &:before{
                background-image: url('../../img/coin.svg');
                background-repeat: no-repeat;
                
                }
            }

                .reward--coin{
                    &:before{
                        background-image: url('../../img/diamond.svg');
                        background-repeat: no-repeat;
                    }
                }
            }


        }

       
    }
}

.profile{

    display: flex;

    &__coins{
        height: 25px;
        background: rgba(145, 152, 148, 0.4);
        flex: 1;
        margin-top: 20px;
        margin-left: 20px;
        border-radius: 3px;
        color: #fff;
        padding: 2px 0 5px 30px;
        position: relative;
        font-size: 16px;

        &::before{
            content: '';
            position: absolute;
            height: 35px;
            width: 35px;
            background-image: url('../../img/coin.svg');
            left: -8px;
            top: -1px;
            background-repeat: no-repeat;
        }
    }

    &__germs{
        height: 25px;
        background: rgba(145, 152, 148, 0.4);
        flex: 1;
        margin-top: 20px;
        margin-right: 20px;
        margin-left: 20px;
        border-radius: 3px;
        color: #fff;
        padding: 2px 0 5px 30px;
        position: relative;
        font-size: 16px;

        &::before{
            content: '';
            position: absolute;
            height: 35px;
            width: 35px;
            background-image: url('../../img/diamond.svg');
            left: -14px;
            top: -1px;
            background-repeat: no-repeat;
        }
    }

    &__user{
        flex: 1;
        max-width: 80px;
        height: 80px;
        margin: 10px 10px;
    }
}





